@import url(//fonts.googleapis.com/css?family=Roboto+Condensed:400,700,300);
@import url(//fonts.googleapis.com/css?family=Playfair+Display:400,700,400italic,700italic);

body {
    background: #282828;
    font-family: 'Roboto Condensed', sans-serif;
    color: #625953;
}
header {
    position: relative;

}
header.inner{
    height:450px;
    overflow:hidden;
}
.row {
    max-width: 98.5rem;
}
.centered {
    text-align: center;
}
.parchment-background {
    background: url(../img/parchment.jpg) repeat scroll 0 0 #fff;
    padding: 50px 0;
    z-index: 0;
}
.wood-bg {
    margin: 0 auto;
    padding: 50px 0;
    -webkit-box-shadow: 0 0 200px #000 inset;
    box-shadow: 0 0 200px #000 inset;
    background: url(../img/wood-bg.jpg) repeat-y scroll 0 0 #fff;
}
.section-shadow {
    -webkit-box-shadow: 0 0 200px #000 inset;
    box-shadow: 0 0 200px #000 inset;
    padding: 50px 0;
}



section:after {
    clear: both;
    content: "";
    display: block;
}
a, a:visited {
    color: #4bc5be;
}
a:hover, a:focus {
    color: #61dad3;
}
p {
    font-weight: 300;
}
ul {
    font-weight: 300;
    margin-left: 0;
}
h1 {
    font-family: 'Roboto Condensed', sans-serif;
    text-transform: uppercase;
    color: #625953;
    font-weight: 700;
    width: 100%;
    font-size: 60px;
    margin-bottom: 40px;
    text-align: left;
    line-height: 90%;
}
h1.center {
    text-align: center;
}
h1.dark {
    color: #fff;
}
h1 span {
    display: block;
    font-family: 'Playfair Display', serif;
    font-weight: 300;
    font-style: italic;
    font-size: 30px;
    text-transform: none;
}
h2 {
    font-family: 'Roboto Condensed', sans-serif;
    text-transform: uppercase;
    color: #625953;
    font-weight: 700;
    width: 100%;
    font-size: 45px;
    margin-bottom: 25px;
}
em {
    font-family: 'Playfair Display', serif;
    font-size: 0.95em;
}
h2 em {
    text-transform: none;
    font-weight: 300;
}
h2.dark {
    color: #fff;
}
h3 {
    font-family: 'Playfair Display', serif;
    font-weight: 300;
    font-style: italic;
    color: #625953;
    width: 100%;
    text-align: center;
    font-size: 30px;
    margin: 30px 0;
}
h3.dark {
    color: #fff;
}
h4 {
    font-family: 'Roboto Condensed', sans-serif;
    text-transform: uppercase;
    color: #625953;
    font-weight: 700;
    width: 100%;
    font-size: 24px;
}
h4.dark {
    color: #fff;
}
h4.headlines {
    margin-bottom: 80px;
    margin-top: 30px;
}
h5, h3.post-title, .catalogueItem h3, .catalogueItemLast h3 {
    font-family: 'Roboto Condensed', sans-serif;
    text-transform: uppercase;
    color: #625953;
    font-weight: 700;
    width: 100%;
    font-size: 20px;
}
h5.dark {
    color: #fff;
}
h6 {
    font-family: 'Roboto Condensed', sans-serif;
    color: #625953;
    text-transform: uppercase;
    font-weight: 700;
    font-size: 16px;
}
h1 a, h2 a, h3 a, h4 a, h5 a, h6 a, h1 a:visited, h2 a:visited, h3 a:visited, h4 a:visited, h5 a:visited, h6 a:visited {
    color: #625953;
}
.button, button {
    background-color: #4bc5be;
}
button:visited, .button:visited {
    color: #fff;
}
.button:hover, button:hover, .button:focus, button:focus {
    background-color: #36a7a1;
}
.button.secondary, button.secondary {
    background-color:#ce3b59;	
}
input[type="text"], input[type="password"], input[type="date"], input[type="datetime"], input[type="datetime-local"], input[type="month"], input[type="week"], input[type="email"], input[type="number"], input[type="search"], input[type="tel"], input[type="time"], input[type="url"], textarea, label {
    font-family: "Helvetica Neue", "Helvetica", Helvetica, Arial, sans-serif;
}
/*nav*/
.main-navigation {
    height: 75px;
    margin: -75px auto 0;
    background: rgba(61,56,56,0.8);
    width: 100%;
    position: relative;
    z-index:1000;
    .top-bar-section ul li>a {
        font-family: 'Roboto Condensed', sans-serif;
        text-transform: uppercase;
        font-size: 18px;
        font-weight: 700;
    }
    .top-bar {
        background: 0 0;
    }
    .top-bar-section>ul {
        display: block;
        text-align: center;
        width: 100%;
    }
    .top-bar-section>ul li {
        background: 0 0;
        display: inline-block;
        float: none;
    }
    .top-bar-section .back {
        display: none;
    }
    .top-bar-section>ul li:hover {
        background: none repeat scroll 0 0 rgba(0,0,0,.5);
    }
    .top-bar-section li:not(.has-form) a:not(.button) {
        background: 0 0;
        padding: 15px 30px;
        min-width: 140px;
    }
    .top-bar-section li:not(.has-form) .dropdown a:not(.button) {
        background: 0 0;
        padding: 5px 35px;
    }
    .dropdown {
        background: rgba(61,56,56,0.98);
        text-align: left;
    }
    .top-bar-section ul li {
        position: relative;
    }
    .top-bar-section ul li:after {
        background: none repeat scroll 0 0 #6a615b;
        content: "";
        display: inline-block;
        height: 5px;
        position: absolute;
        right: -2.5px;
        top: 0;
        bottom: 0;
        margin: auto;
        -webkit-transform: rotate(45deg);
        -ms-transform: rotate(45deg);
        transform: rotate(45deg);
        width: 5px;
        border: none;
    }
    .top-bar-section .has-dropdown>a:after {
        margin-right: 0;
        display: none;
    }
    .top-bar-section .has-dropdown>a {
        padding-right: 15px!important;
    }
    .top-bar-section>ul>li .dropdown li:after {
        display: none;
    }
    .top-bar-section>ul>li:last-child:after {
        display: none;
    }
}
.utility-nav {
    margin: 0;
    position: absolute;
    width: 100%;
    z-index: 1;
}
.utility-nav ul, .utility-nav ul li {
    list-style: none outside none;
    margin: 0;
    padding: 0;
}
.utility-nav li {
    float: left;
}
.utility-nav li span {
    color: #eaedf2;
    margin-right: 5px;
}
.utility-nav li a {
    color: #eaedf2;
    font-family: 'Roboto Condensed', sans-serif;
    text-transform: uppercase;
    font-size: 14px;
    font-weight: 400;
    padding: 5px 12px;
    position: relative;
    top: -2px;
}
.utility-nav li:hover > a, .utility-nav li:hover > a span {
    color: #fff;
}
.utility-nav ul {
    margin-top:20px;	
}
.utility-nav ul ul {
    margin-top:0px;	
}
.shopping-cart {
    color: #ffffff;
    font-family: 'Roboto Condensed', sans-serif;
    text-transform: uppercase;
    font-size: 14px;
    float:right;
}
a.cartSummaryLink {
    color: #ce3b59 ;
}
.utility-nav .uty0, .utility-nav .uty1 {
    display: none;
}
table.cartSummaryTable {
    background: none repeat scroll 0 0 rgba(0, 0, 0, 0);
    border: medium none;
    margin: 0;
    display:inline-block;
}
td.cartSummaryItem {
    color: #ffffff;
    font-family: 'Roboto Condensed', sans-serif;
    text-transform: uppercase;
    font-size: 14px;
    padding: 2px 0;
}
.search .search-wrap {

    position: relative;
    top:10px;
    width: 260px;
}
.search input[type="text"] {
    border: medium none;
    border-radius: 3px;
    font-family: 'Roboto Condensed', sans-serif;
    text-transform: uppercase;
    font-size: 14px;
}
.search input[type="submit"] , .search input[type="submit"]:focus{
    height: 28px;
    overflow: hidden;
    position: absolute;
    right: 5px;
    top: 4px;
    font-family: 'Roboto Condensed', sans-serif;
    text-transform: uppercase;
    font-size: 14px;
    padding: 0 28px;
}
/*hero*/

.hero-slider {
    margin-bottom: 0;
    margin: 0 auto;
}
.hero-slider .slick-prev:before, .hero-slider .slick-next:before {
    display: block;
    z-index: 1;
}
.hero-slider .slick-prev, .hero-slider .slick-next {
    height: 88px;
    width: 35px;
    opacity: .6;
}
.hero-slider .slick-prev:hover, .hero-slider .slick-next:hover {
    opacity: 1;
}
.hero-slider .slick-prev {
    left: 15px;
    background: url(../img/prev.svg) no-repeat scroll 0 0 rgba(0,0,0,0);
}
.hero-slider .slick-prev:before {
    display: none;
}
.hero-slider .slick-next {
    right: 15px;
    background: url(../img/next.svg) no-repeat scroll 0 0 rgba(0,0,0,0);
}
.hero-slider .slick-next:before {
    display: none;
}
.slick-slide img {
    margin: 0 auto;
}
.slider {
    position: relative;
    z-index: 0;
}
.slider .logo img {
    position: absolute;
    top: 0;
    z-index: 100;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
}
.slick-prev, .slick-next {
    z-index: 100;
}
.slick-dots li button:before {
    color:#ce3b59;
    font-size: 8px;	
}
.hero-container {
    position: relative;
    z-index: 0;
}
.hero-container .hero {
    position: relative;
}
.hero-container .hero img {
    margin: 0 auto;
    display: block;
}
.hero-container .hero div:after {
    position: absolute;
    height: 100%;
    width: 100%;
    content: "";
    -webkit-box-shadow: 0 100px 200px rgba(0,0,0,.7)inset;
    box-shadow: 0 100px 200px rgba(0,0,0,.7)inset;
    top: 0;
}
.hero-container .logo img {
    position: absolute;
    top: -50%;
    z-index: 1;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    max-width: 376px;
}
