input[type="submit"],  input[type="submit"]:visited, .button, button,  input[type="submit"]:focus  {
    background: none repeat scroll 0 0 #4bc5be;
    border: medium none;
    color: #fff;
    display: block;
    font-family: "Roboto Condensed",sans-serif;
    font-weight: 700;
    line-height: 100%;
    padding: 15px;
    text-align: center;
    text-transform: uppercase;
    border-radius:0px;
    margin-bottom:1rem;
}
button.radius, .button.radius {
    border-radius: 0;
}
.more-info, .more-info:visited, .more-info:focus {
    background: none repeat scroll 0 0 rgba(0, 0, 0, 0);
    border-color: #4bc5be rgba(0, 0, 0, 0);
    border-style: solid;
    border-width: 20px 10px;
    color: #fff;
    display: inline-block;
    font-size: 0;
    height: 0;
    line-height: 0;
    padding: 0;
    transition: all 250ms ease 0s;
    width: auto;
}
.more-info.center {
    display: block;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
    width: 280px;
}
.more-info.accent {
    border-top: 20px solid #ce3b59;
    border-left: 10px solid rgba(0,0,0,0);
    border-right: 10px solid rgba(0,0,0,0);
    border-bottom: 20px solid #ce3b59;
}
.more-info.accent:hover, .more-info.accent:focus {
    border-top: 20px solid #ab2b45;
    border-bottom: 20px solid #ab2b45;
}
.more-info:hover, .more-info:focus {
    border-top: 20px solid #36a7a1;
    border-bottom: 20px solid #36a7a1;
    background: 0 0;
    -webkit-transition: all 250ms ease;
    transition: all 250ms ease;
    color:#fff;
}
.more-info span {
    font-size: 16px;
    line-height: 0;
    width: 0;
    font-family: 'Roboto Condensed', sans-serif;
    text-transform: uppercase;
    font-weight: 700;
    padding: 0 30px;
    position: relative;
}
/*page-header*/
.page-header {
    -webkit-box-shadow: 0 0 200px #000 inset;
    box-shadow: 0 0 200px #000 inset;
    padding: 50px 0;
    background: url(../img/bg-tile.jpg);
}
.page-header h1 {
    margin-bottom: 0;
}
.page-content {
    position: relative;
    background: url(../img/parchment.jpg) repeat scroll 0 0 #fff;
    padding: 50px 0;
    z-index: 0;
}
.page-content:after {
    background: url(../img/faded-logo.png) no-repeat scroll 0 0 rgba(0,0,0,0);
    bottom: 0;
    content: "";
    height: 400px;
    position: absolute;
    right: 0;
    width: 250px;
    opacity: .1;
    z-index: -1;
}
/*sub-navigation*/
.sub-navigation {
    background: url(../img/parchment.jpg) repeat scroll 0 0 #fff;
    padding: 50px 0;
    z-index: 0;
    position: relative;
    padding: 10px 0;
    background-color: #4bc5be;
    background-blend-mode: multiply;
    text-transform: uppercase;
    font-weight: 700;
    color: #fff;
    text-align: center;
}
.sub-navigation a {
    margin-right: 20px;
    color: #fff;
    position: relative;
}
.sub-navigation a:after {
    content: "";
    height: 3px;
    width: 3px;
    border-radius: 3px;
    display: inline-block;
    position: absolute;
    background: #fff;
    top: 0;
    bottom: 0;
    right: -11px;
    margin: auto;
}
.sub-navigation a:last-child:after {
    display: none;
}
.item-name {
    font-weight: 700;
    text-transform: uppercase;
}
.round-image img {
    border-radius: 50%;
}
/*headlines*/
.headlines {
    overflow: hidden;
    text-align: center;
}
.headlines>span {
    position: relative;
    display: inline-block;
}
.headlines>span:before, .headlines>span:after {
    content: '';
    position: absolute;
    top: 50%;
    border-bottom: 1px solid rgba(0, 0, 0, 0.25);
    width: 591px;
    margin: 0 20px;
}
.headlines.dark > span:before, .headlines.dark >span:after {
    border-bottom: 1px solid rgba(255, 255, 255, 0.25);
}
.headlines>span:before {
    right: 100%;
}
.headlines>span:after {
    left: 100%;
}
.sub-headline {
    color: #625953;
    font-family: "Roboto Condensed", sans-serif;
    font-size: 16px;
    line-height: 150%;
    margin: 0 auto 40px;
    max-width: 750px;
    text-align: center;
}
.sub-headline.dark {
    color: #fff;
}
