@import url(//fonts.googleapis.com/css?family=Roboto+Condensed:400,700,300);
@import url(//fonts.googleapis.com/css?family=Playfair+Display:400,700,400italic,700italic);
body {
  background: #282828;
  font-family: 'Roboto Condensed', sans-serif;
  color: #625953;
}
header {
  position: relative;
}
header.inner {
  height: 450px;
  overflow: hidden;
}
.row {
  max-width: 98.5rem;
}
.centered {
  text-align: center;
}
.parchment-background {
  background: url(../img/parchment.jpg) repeat scroll 0 0 #ffffff;
  padding: 50px 0;
  z-index: 0;
}
.wood-bg {
  margin: 0 auto;
  padding: 50px 0;
  -webkit-box-shadow: 0 0 200px #000 inset;
  box-shadow: 0 0 200px #000 inset;
  background: url(../img/wood-bg.jpg) repeat-y scroll 0 0 #ffffff;
}
.section-shadow {
  -webkit-box-shadow: 0 0 200px #000 inset;
  box-shadow: 0 0 200px #000 inset;
  padding: 50px 0;
}
section:after {
  clear: both;
  content: "";
  display: block;
}
a,
a:visited {
  color: #4bc5be;
}
a:hover,
a:focus {
  color: #61dad3;
}
p {
  font-weight: 300;
}
ul {
  font-weight: 300;
  margin-left: 0;
}
h1 {
  font-family: 'Roboto Condensed', sans-serif;
  text-transform: uppercase;
  color: #625953;
  font-weight: 700;
  width: 100%;
  font-size: 60px;
  margin-bottom: 40px;
  text-align: left;
  line-height: 90%;
}
h1.center {
  text-align: center;
}
h1.dark {
  color: #fff;
}
h1 span {
  display: block;
  font-family: 'Playfair Display', serif;
  font-weight: 300;
  font-style: italic;
  font-size: 30px;
  text-transform: none;
}
h2 {
  font-family: 'Roboto Condensed', sans-serif;
  text-transform: uppercase;
  color: #625953;
  font-weight: 700;
  width: 100%;
  font-size: 45px;
  margin-bottom: 25px;
}
em {
  font-family: 'Playfair Display', serif;
  font-size: 0.95em;
}
h2 em {
  text-transform: none;
  font-weight: 300;
}
h2.dark {
  color: #fff;
}
h3 {
  font-family: 'Playfair Display', serif;
  font-weight: 300;
  font-style: italic;
  color: #625953;
  width: 100%;
  text-align: center;
  font-size: 30px;
  margin: 30px 0;
}
h3.dark {
  color: #fff;
}
h4 {
  font-family: 'Roboto Condensed', sans-serif;
  text-transform: uppercase;
  color: #625953;
  font-weight: 700;
  width: 100%;
  font-size: 24px;
}
h4.dark {
  color: #fff;
}
h4.headlines {
  margin-bottom: 80px;
  margin-top: 30px;
}
h5,
h3.post-title,
.catalogueItem h3,
.catalogueItemLast h3 {
  font-family: 'Roboto Condensed', sans-serif;
  text-transform: uppercase;
  color: #625953;
  font-weight: 700;
  width: 100%;
  font-size: 20px;
}
h5.dark {
  color: #fff;
}
h6 {
  font-family: 'Roboto Condensed', sans-serif;
  color: #625953;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 16px;
}
h1 a,
h2 a,
h3 a,
h4 a,
h5 a,
h6 a,
h1 a:visited,
h2 a:visited,
h3 a:visited,
h4 a:visited,
h5 a:visited,
h6 a:visited {
  color: #625953;
}
.button,
button {
  background-color: #4bc5be;
}
button:visited,
.button:visited {
  color: #fff;
}
.button:hover,
button:hover,
.button:focus,
button:focus {
  background-color: #36a7a1;
}
.button.secondary,
button.secondary {
  background-color: #ce3b59;
}
input[type="text"],
input[type="password"],
input[type="date"],
input[type="datetime"],
input[type="datetime-local"],
input[type="month"],
input[type="week"],
input[type="email"],
input[type="number"],
input[type="search"],
input[type="tel"],
input[type="time"],
input[type="url"],
textarea,
label {
  font-family: "Helvetica Neue", "Helvetica", Helvetica, Arial, sans-serif;
}
/*nav*/
.main-navigation {
  height: 75px;
  margin: -75px auto 0;
  background: rgba(61, 56, 56, 0.8);
  width: 100%;
  position: relative;
  z-index: 1000;
}
.main-navigation .top-bar-section ul li > a {
  font-family: 'Roboto Condensed', sans-serif;
  text-transform: uppercase;
  font-size: 18px;
  font-weight: 700;
}
.main-navigation .top-bar {
  background: 0 0;
}
.main-navigation .top-bar-section > ul {
  display: block;
  text-align: center;
  width: 100%;
}
.main-navigation .top-bar-section > ul li {
  background: 0 0;
  display: inline-block;
  float: none;
}
.main-navigation .top-bar-section .back {
  display: none;
}
.main-navigation .top-bar-section > ul li:hover {
  background: none repeat scroll 0 0 rgba(0, 0, 0, 0.5);
}
.main-navigation .top-bar-section li:not(.has-form) a:not(.button) {
  background: 0 0;
  padding: 15px 30px;
  min-width: 140px;
}
.main-navigation .top-bar-section li:not(.has-form) .dropdown a:not(.button) {
  background: 0 0;
  padding: 5px 35px;
}
.main-navigation .dropdown {
  background: rgba(61, 56, 56, 0.98);
  text-align: left;
}
.main-navigation .top-bar-section ul li {
  position: relative;
}
.main-navigation .top-bar-section ul li:after {
  background: none repeat scroll 0 0 #6a615b;
  content: "";
  display: inline-block;
  height: 5px;
  position: absolute;
  right: -2.5px;
  top: 0;
  bottom: 0;
  margin: auto;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
  width: 5px;
  border: none;
}
.main-navigation .top-bar-section .has-dropdown > a:after {
  margin-right: 0;
  display: none;
}
.main-navigation .top-bar-section .has-dropdown > a {
  padding-right: 15px!important;
}
.main-navigation .top-bar-section > ul > li .dropdown li:after {
  display: none;
}
.main-navigation .top-bar-section > ul > li:last-child:after {
  display: none;
}
.utility-nav {
  margin: 0;
  position: absolute;
  width: 100%;
  z-index: 1;
}
.utility-nav ul,
.utility-nav ul li {
  list-style: none outside none;
  margin: 0;
  padding: 0;
}
.utility-nav li {
  float: left;
}
.utility-nav li span {
  color: #eaedf2;
  margin-right: 5px;
}
.utility-nav li a {
  color: #eaedf2;
  font-family: 'Roboto Condensed', sans-serif;
  text-transform: uppercase;
  font-size: 14px;
  font-weight: 400;
  padding: 5px 12px;
  position: relative;
  top: -2px;
}
.utility-nav li:hover > a,
.utility-nav li:hover > a span {
  color: #fff;
}
.utility-nav ul {
  margin-top: 20px;
}
.utility-nav ul ul {
  margin-top: 0px;
}
.shopping-cart {
  color: #ffffff;
  font-family: 'Roboto Condensed', sans-serif;
  text-transform: uppercase;
  font-size: 14px;
  float: right;
}
a.cartSummaryLink {
  color: #ce3b59 ;
}
.utility-nav .uty0,
.utility-nav .uty1 {
  display: none;
}
table.cartSummaryTable {
  background: none repeat scroll 0 0 rgba(0, 0, 0, 0);
  border: medium none;
  margin: 0;
  display: inline-block;
}
td.cartSummaryItem {
  color: #ffffff;
  font-family: 'Roboto Condensed', sans-serif;
  text-transform: uppercase;
  font-size: 14px;
  padding: 2px 0;
}
.search .search-wrap {
  position: relative;
  top: 10px;
  width: 260px;
}
.search input[type="text"] {
  border: medium none;
  border-radius: 3px;
  font-family: 'Roboto Condensed', sans-serif;
  text-transform: uppercase;
  font-size: 14px;
}
.search input[type="submit"],
.search input[type="submit"]:focus {
  height: 28px;
  overflow: hidden;
  position: absolute;
  right: 5px;
  top: 4px;
  font-family: 'Roboto Condensed', sans-serif;
  text-transform: uppercase;
  font-size: 14px;
  padding: 0 28px;
}
/*hero*/
.hero-slider {
  margin-bottom: 0;
  margin: 0 auto;
}
.hero-slider .slick-prev:before,
.hero-slider .slick-next:before {
  display: block;
  z-index: 1;
}
.hero-slider .slick-prev,
.hero-slider .slick-next {
  height: 88px;
  width: 35px;
  opacity: .6;
}
.hero-slider .slick-prev:hover,
.hero-slider .slick-next:hover {
  opacity: 1;
}
.hero-slider .slick-prev {
  left: 15px;
  background: url(../img/prev.svg) no-repeat scroll 0 0 rgba(0, 0, 0, 0);
}
.hero-slider .slick-prev:before {
  display: none;
}
.hero-slider .slick-next {
  right: 15px;
  background: url(../img/next.svg) no-repeat scroll 0 0 rgba(0, 0, 0, 0);
}
.hero-slider .slick-next:before {
  display: none;
}
.slick-slide img {
  margin: 0 auto;
}
.slider {
  position: relative;
  z-index: 0;
}
.slider .logo img {
  position: absolute;
  top: 0;
  z-index: 100;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
}
.slick-prev,
.slick-next {
  z-index: 100;
}
.slick-dots li button:before {
  color: #ce3b59;
  font-size: 8px;
}
.hero-container {
  position: relative;
  z-index: 0;
}
.hero-container .hero {
  position: relative;
}
.hero-container .hero img {
  margin: 0 auto;
  display: block;
}
.hero-container .hero div:after {
  position: absolute;
  height: 100%;
  width: 100%;
  content: "";
  -webkit-box-shadow: 0 100px 200px rgba(0, 0, 0, 0.7) inset;
  box-shadow: 0 100px 200px rgba(0, 0, 0, 0.7) inset;
  top: 0;
}
.hero-container .logo img {
  position: absolute;
  top: -50%;
  z-index: 1;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  max-width: 376px;
}
/*cta*/
.cta-container {
  text-align: center;
  background: url(../img/bg-tile.jpg);
  box-shadow: 0 0 200px #000 inset;
}
.cta-container .more-info {
  margin-top: 30px;
}
.cta-container .left-cta,
.cta-container .right-cta {
  margin: 0;
  padding: 0;
  max-width: 800px;
  display: inline-block;
  position: relative;
}
.cta-container .full-cta,
.cta-container .trans-cta {
  margin: 0;
  padding: 0;
  position: relative;
  height: 315px;
  overflow: hidden;
}
.cta-container .full-cta img {
  position: absolute;
  top: 0;
  left: 0;
  max-width: inherit;
  height: 100%;
}
.cta-container .trans-cta img {
  position: absolute;
  top: 0;
  right: 0;
}
.cta-container .left-cta {
  text-align: right;
  margin-right: -5px;
}
.cta-container .cta-text {
  bottom: 0;
  clear: both;
  color: #fff;
  display: block;
  margin: auto;
  padding: 65px 80px;
  position: absolute;
  text-align: center;
  text-shadow: 0 0 5px rgba(0, 0, 0, 0.4);
  text-transform: uppercase;
  top: 0;
  width: 100%;
}
.cta-container .full-cta .cta-text {
  padding: 72px 0 ;
}
.cta-container .trans-cta .cta-text {
  padding: 50px 0;
}
.cta-container .cta-text .cta-header {
  display: block;
  clear: both;
  font-size: 64px;
  font-weight: 700;
  line-height: 130%;
}
.cta-container .full-cta .cta-text .cta-header,
.cta-container .trans-cta .cta-text .cta-header {
  font-size: 90px;
  line-height: 85%;
  margin-bottom: 10px;
}
.cta-container .trans-cta .cta-text .cta-header {
  font-size: 70px;
}
.cta-container .cta-text .cta-subtitle {
  display: block;
  clear: both;
  font-weight: 700;
  font-size: 20px;
}
.cta-container .cta-text .cta-sub-header {
  display: block;
  clear: both;
  font-size: 22px;
  font-weight: 300;
  line-height: 0%;
  text-transform: none;
}
.cta-container .cta-text .cta-header-1 {
  display: block;
  clear: both;
  font-size: 90px;
  font-weight: 300;
  line-height: 120%;
  text-transform: none;
  font-family: 'Playfair Display', serif;
  font-style: italic;
}
.trans-cta {
  margin: 0;
  padding: 0;
  display: inline-block;
  position: relative;
  text-align: right;
  width: 100%;
}
.trans-cta .cta-text-container {
  max-width: 62.5rem;
  margin: 0 auto;
}
.trans-cta .cta-text {
  text-align: left;
}
.trans-cta .cta-text .cta-header {
  font-size: 60px;
}
.trans-cta .cta-text .cta-subtitle {
  font-family: 'Playfair Display', serif;
  font-weight: 300;
  font-style: italic;
  text-transform: none;
  font-size: 20px;
}
input[type="submit"],
input[type="submit"]:visited,
.button,
button,
input[type="submit"]:focus {
  background: none repeat scroll 0 0 #4bc5be;
  border: medium none;
  color: #fff;
  display: block;
  font-family: "Roboto Condensed", sans-serif;
  font-weight: 700;
  line-height: 100%;
  padding: 15px;
  text-align: center;
  text-transform: uppercase;
  border-radius: 0px;
  margin-bottom: 1rem;
}
button.radius,
.button.radius {
  border-radius: 0;
}
.more-info,
.more-info:visited,
.more-info:focus {
  background: none repeat scroll 0 0 rgba(0, 0, 0, 0);
  border-color: #4bc5be rgba(0, 0, 0, 0);
  border-style: solid;
  border-width: 20px 10px;
  color: #fff;
  display: inline-block;
  font-size: 0;
  height: 0;
  line-height: 0;
  padding: 0;
  transition: all 250ms ease 0s;
  width: auto;
}
.more-info.center {
  display: block;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  width: 280px;
}
.more-info.accent {
  border-top: 20px solid #ce3b59;
  border-left: 10px solid rgba(0, 0, 0, 0);
  border-right: 10px solid rgba(0, 0, 0, 0);
  border-bottom: 20px solid #ce3b59;
}
.more-info.accent:hover,
.more-info.accent:focus {
  border-top: 20px solid #ab2b45;
  border-bottom: 20px solid #ab2b45;
}
.more-info:hover,
.more-info:focus {
  border-top: 20px solid #36a7a1;
  border-bottom: 20px solid #36a7a1;
  background: 0 0;
  -webkit-transition: all 250ms ease;
  transition: all 250ms ease;
  color: #fff;
}
.more-info span {
  font-size: 16px;
  line-height: 0;
  width: 0;
  font-family: 'Roboto Condensed', sans-serif;
  text-transform: uppercase;
  font-weight: 700;
  padding: 0 30px;
  position: relative;
}
/*page-header*/
.page-header {
  -webkit-box-shadow: 0 0 200px #000 inset;
  box-shadow: 0 0 200px #000 inset;
  padding: 50px 0;
  background: url(../img/bg-tile.jpg);
}
.page-header h1 {
  margin-bottom: 0;
}
.page-content {
  position: relative;
  background: url(../img/parchment.jpg) repeat scroll 0 0 #ffffff;
  padding: 50px 0;
  z-index: 0;
}
.page-content:after {
  background: url(../img/faded-logo.png) no-repeat scroll 0 0 rgba(0, 0, 0, 0);
  bottom: 0;
  content: "";
  height: 400px;
  position: absolute;
  right: 0;
  width: 250px;
  opacity: .1;
  z-index: -1;
}
/*sub-navigation*/
.sub-navigation {
  background: url(../img/parchment.jpg) repeat scroll 0 0 #ffffff;
  padding: 50px 0;
  z-index: 0;
  position: relative;
  padding: 10px 0;
  background-color: #4bc5be;
  background-blend-mode: multiply;
  text-transform: uppercase;
  font-weight: 700;
  color: #fff;
  text-align: center;
}
.sub-navigation a {
  margin-right: 20px;
  color: #fff;
  position: relative;
}
.sub-navigation a:after {
  content: "";
  height: 3px;
  width: 3px;
  border-radius: 3px;
  display: inline-block;
  position: absolute;
  background: #fff;
  top: 0;
  bottom: 0;
  right: -11px;
  margin: auto;
}
.sub-navigation a:last-child:after {
  display: none;
}
.item-name {
  font-weight: 700;
  text-transform: uppercase;
}
.round-image img {
  border-radius: 50%;
}
/*headlines*/
.headlines {
  overflow: hidden;
  text-align: center;
}
.headlines > span {
  position: relative;
  display: inline-block;
}
.headlines > span:before,
.headlines > span:after {
  content: '';
  position: absolute;
  top: 50%;
  border-bottom: 1px solid rgba(0, 0, 0, 0.25);
  width: 591px;
  margin: 0 20px;
}
.headlines.dark > span:before,
.headlines.dark > span:after {
  border-bottom: 1px solid rgba(255, 255, 255, 0.25);
}
.headlines > span:before {
  right: 100%;
}
.headlines > span:after {
  left: 100%;
}
.sub-headline {
  color: #625953;
  font-family: "Roboto Condensed", sans-serif;
  font-size: 16px;
  line-height: 150%;
  margin: 0 auto 40px;
  max-width: 750px;
  text-align: center;
}
.sub-headline.dark {
  color: #fff;
}
/*featured drinks*/
.featured-drinks {
  -webkit-box-shadow: 0 0 170px #000 inset;
  box-shadow: 0 0 200px #000 inset;
  padding: 50px 0;
  text-align: center;
  position: relative;
  margin: 0 auto;
  max-width: 1600px;
  background: url(../img/bg-tile.jpg);
}
.featured-drinks:after {
  background: url(../img/faded-logo.png) no-repeat scroll 0 0 rgba(0, 0, 0, 0);
  bottom: 0;
  content: "";
  height: 400px;
  position: absolute;
  right: 0;
  width: 250px;
  opacity: .3;
  z-index: 1;
}
.featured-drinks h4 {
  font-size: 36px;
}
.featured-drinks .drink-description {
  color: #fff;
  line-height: 130%;
}
.featured-drinks .drink-space {
  margin: 80px 0 40px;
}
.featured-drinks img {
  height: 480px;
  position: relative;
  z-index: 100;
}
.featured-drinks .more-info.accent {
  margin: 20px auto;
}
/*events*/
.events {
  background: url(../img/parchment.jpg) repeat scroll 0 0 #ffffff;
  padding: 50px 0;
  z-index: 0;
  max-width: 1600px;
  margin: 0 auto;
}
.event {
  margin-bottom: 30px;
  overflow: hidden;
}
.event .date-container {
  padding: 0 5px;
}
.event .date-container .date {
  background: #4bc5be;
  color: #fff;
  font-size: 18px;
  line-height: 100%;
  font-weight: 700;
  text-align: center;
  width: 100%;
  display: block;
  padding: 10px;
}
.event .event-name,
.news-item h3,
h3.post-title,
.catalogueItem h3,
.catalogueItemLast h3 {
  display: block;
  font-style: normal;
  font-weight: 700;
  margin: 0 0 5px;
  text-align: left;
  text-transform: uppercase;
}
.event-image {
  margin-bottom: 25px;
}
.event-image:hover {
  -webkit-box-shadow: 0 0 15px rgba(0, 0, 0, 0.7);
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.7);
}
.event-image .event-banner {
  background: #4bc5be;
  padding: 15px 25px;
}
.event-image .event-banner .event-banner-text {
  color: #fff;
  text-transform: uppercase;
  font-weight: 700;
  margin-right: 50px;
}
.event-image .event-banner .more-info {
  border-top: 20px solid #fff;
  border-left: 10px solid rgba(0, 0, 0, 0);
  border-right: 10px solid rgba(0, 0, 0, 0);
  border-bottom: 20px solid #fff;
  color: #4bc5be;
  margin-bottom: 0;
}
.event-image .event-banner .more-info:hover {
  border-top: 20px solid #e6e6e6;
  border-bottom: 20px solid #e6e6e6;
}
.event-image .more-info span {
  padding: 0 15px;
}
.event-details {
  padding: 10px 0;
}
.visit-us {
  margin: 0 auto;
  padding: 50px 0;
  -webkit-box-shadow: 0 0 200px #000 inset;
  box-shadow: 0 0 200px #000 inset;
  background: url(../img/wood-bg.jpg) repeat-y scroll 0 0 #ffffff;
}
.visit-us .more-info {
  margin-top: 35px;
}
/*footer*/
footer {
  background: #3d3831;
  padding: 15px 0;
  font-size: 13px;
}
footer .copy {
  text-transform: uppercase;
}
footer .copy a {
  margin-right: 20px;
  color: #fff;
  position: relative;
}
footer .copy a:after {
  content: "";
  height: 3px;
  width: 3px;
  border-radius: 3px;
  display: inline-block;
  position: absolute;
  background: #fff;
  top: 0;
  bottom: 0;
  right: -11px;
  margin: auto;
}
footer .social-icons {
  text-align: right;
}
footer .social-icons a {
  color: #fff;
  font-size: 18px;
  margin: 0 10px;
